section {
    margin: 50px auto;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 600px;
    background-color: antiquewhite;
    box-shadow: rgba(68, 68, 109, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;;
  }
  
  h1 {
      font-size: 30px;
      font-weight: 700;
  }
  
  h2 {
      font-size: 20px;
      font-weight: 700; 
  }